 console.log(initialstate.foods)
 const Reducer=(state=initialstate,action)=>{

    switch(action.type){
        case actiontype.ADDORDER:
            const dig=state.foods[action.idadd].some
            const add=[...state.foods];
            add[action.idadd]={
                ...state.foods[action.idadd],
                some: dig + 1,
                totalpriceone: (dig + 1) * state.foods[action.idadd].price
            }
            const total= (dig + 1) * state.foods[action.idadd].price
            return{
                ...state,
               foods:add,
               totalpice: total
            }
         case actiontype.LESSORDER:
              const fig= state.foods[action.idless].some;
                const less=[...state.foods];
                let lessprice= 0;
                if(state.foods[action.idless].totalpriceone !== 0 && state.foods[action.idless].some){
                    lessprice= state.foods[action.idless].totalpriceone - state.foods[action.idless].price
                }
                less[action.idless]={
                    ...state.foods[action.idless],
                    some: fig - 1,
                    totalpriceone:state.foods[action.idless].totalpriceone < state.foods[action.idless].price? 0:lessprice, 
                }
                const tot= state.foods[action.idless].totalpriceone - state.foods[action.idless].price
                    return{
                        ...state,
                        foods: less,
                        totalpice: tot
                    }
         case actiontype.BABRENG:
                let pricebrenge= 0;
                if(action.tr === 'بابرنج'){
                    pricebrenge= -30000
                }else if(action.tr === 'بدون برنج'){
                    pricebrenge= 30000
                }
                const pr=state.foods[action.idrice].price;
               const brenge=[...state.foods];
               brenge[action.idrice]={
                   ...state.foods[action.idrice],
                   rice: action.tr,
                    price: pr - pricebrenge
               }
              return{
                 ...state,
                 foods: brenge
                    }

         case actiontype.SPECIALLY:
                let pricesp=0;
                if(state.foods[action.idsp].special === 'ویژه'){
                    pricesp= -30000;
                    
                }else if(state.foods[action.idsp].special === 'معمولی'){
                       pricesp= 30000
                }
             const changesp=[...state.foods];
            
             changesp[action.idsp]={
                 ...state.foods[action.idsp],
                 special: action.tr,
                 price: pricesp + state.foods[action.idsp].price
             }
                return{
                     ...state,
                     foods:changesp
                          }
       case actiontype.FILTER:
             return{
                 ...state,
                 filtertxt:action.txt
                     } 
        case actiontype.Rating:
            const objrte= [...state.foods];
             objrte[action.id]={
                    ...state.foods[action.id],
                    ratings:action.rte
            }
            console.log(action.rte)
            return{
                ...state,
                foods: objrte
            }
               default: return state         }

 }

 export default Reducer;