    import React,{useState} from 'react';
    import classes from './Foods.css';
    import {connect} from 'react-redux';
    import Button from '../Button/Button';
import Cardfood from './cardfood/cardfood';
import Modal from '../modal/modal';

    const Foods=(props)=>{
        
        const [show,notshow]=useState(false)
        console.log(props.totolpr)
        const showhandller=()=>{
            notshow(false)
        }
        let page= null;
        if(show){
            page= <Modal showe={showhandller}></Modal>
        }
        return(
            <div className={classes.Foods}>
                {page}
                <div className={classes.pos}><Button disabl={props.totolpr === 0 ? true:false} click={()=>notshow(true)} > ثبت سفارش</Button></div>
                <Cardfood></Cardfood>
            </div>
        );
    }
    const mapstatetoprops=state=>{
        return{
            totolpr:state.card.totalpice
        }
    }
    export default connect(mapstatetoprops, null)(Foods);