import React,{useState,useEffect} from 'react';
import classes from '../ordersumury/ordersumury.css';
import {connect} from 'react-redux';
import * as action from '../../store/action/index';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';

const Auth=(props)=>{
        const [orderform,setorderform]=useState({
            email:{
                tag:'input',
                proptype:{
                    type:'input',
                    placeholder:'ایمیل'
                },
                elementlength:{
                    require:true,
                    maxlength:18,
                    minlength:6,
                    isEmail:true
                },
                value:'',
                touch:false,
                validation:false
            },
            
            password:{
                tag:'input',
                proptype:{
                    type:'input',
                    placeholder:'رمز عبور'
                },
                elementlength:{
                    require:true,
                    maxlength:18,
                    minlength:6,
                },
                value:'',
                touch:false,
                validation:false
            }
            
        })
        let [dis, setdis]=useState(true)
        let dataonj=[];
        for(let key in orderform){
            dataonj.push({
                config: orderform[key],
                id:key
            })
        }
       const validateor=(value,rules)=>{
        let valid  = true;
        if(!rules){
            return true;
        }
        if(rules.require){
            valid = value.trim() !== '' && valid;
        }
        if(rules.maxlength){
            valid = value.length <= rules.maxlength && valid;
        }
        if(rules.minlength){
            valid = value.length >= rules.minlength && valid;
        }
        
    if (rules.isEmail) {
        const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        valid = pattern.test(value) && valid
    }
    
    if (rules.isNumeric) {
        const pattern = /^\d+$/;
        valid = pattern.test(value) && valid
    }
       return valid;
       }
        const changehandller=(event,id)=>{
            const form={
                ...orderform,
                [id]:{
                    ...orderform[id],
                   value: event,
                    touch:true,
                    validation: validateor(event.target.value, orderform[id].elementlength)
                }
            }
            setorderform(form)
            let disab= true;
            for(let key in orderform){
                disab = orderform[key].validation && disab;
                
            }
            setdis(dis)
            
           
        }
        
        const input=dataonj.map(mp=>(
            <Input key={mp.id} proptype={mp.config.proptype}
             value={mp.config.value}
             tag={mp.config.tag}
            optiontxt={mp.config.optiontxt}
            validation={mp.config.validation}
            touch={mp.config.touch}
             change={(event)=>changehandller(event, mp.id)}></Input>
        ))
    return(
        <div style={{height:window.innerHeight,width:window.innerWidth,background:'#fff'}}>
            <div className={classes.head}></div>
             <div className={classes.section}>
                 <div className={classes.box_input}>{input}
               <Button disabl={dis}> ثبت سفارش</Button>
               </div> 
               </div>
        </div>
    );
}

export default Auth;