import React from 'react';
import classes from './modal.css';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
const Modal=(props)=>{
    

    return(
        <div onClick={props.showe} className={classes.modal}  >
            <div className={classes.modal_box} onClick={props.showe}>
                {props.order.map(mp=>(
                    mp.some !== 0 ?<p key={mp.id} className={classes}>{mp.name} : {mp.totalpriceone}</p>:null
                
                ))}
                <div className={classes.modal_button}>
                <button className={classes.button1} click={props.showe}>لغو</button>
                    <Link to="order" className={classes.button2} >ادامه</Link>
                </div>
            </div>
        </div>
    );

}
const mapstatetoprops=state=>{
    return{
        order:state.card.foods
    }
}
export default connect(mapstatetoprops,null)(Modal);