import React,{useState,useEffect} from 'react';
import classes from './ordersumury.css';
import {connect} from 'react-redux';
import * as action from '../../store/action/index';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';

const Ordersumury=(props)=>{
        const [orderform,setorderform]=useState({
            phoneNumber:{
                tag:'input',
                proptype:{
                    type:'input',
                    placeholder:'شماره تلفن'
                },
                elementlength:{
                    require:true,
                    maxlength:14,
                    minlengt:9
                },
                value:'',
                touch:false,
                validation:false
            },
            adress:{
                tag:'texterea',
                proptype:{
                    
                    placeholder:'آدرس'
                },
                elementlength:{
                    require:true,
                    maxlength:140,
                    minlengt:3
                },
                value:'',
                touch:false,
                validation:false
            },
            eshtrak:{
                tag:'input',
                proptype:{
                    type:'input',
                    placeholder:'شماره اشتراک'
                },
                elementlength:{
                    require:true,
                    maxlength:13,
                    minlengt:5
                },
                value:'',
                touch:false,
                validation:false
            },
        })
        let [dis, setdis]=useState(false)
        let dataonj=[];
        for(let key in orderform){
            dataonj.push({
                config: orderform[key],
                id:key
            })
        }
      
        const changehandller=(event,id)=>{
            const form={
                ...orderform,
                [id]:{
                    ...orderform[id],
                   value: event.target.value,
                    touch:true,
                    validation: validateor(event.target.value, orderform[id].elementlength)
                }
            }
            setorderform(form)
            let disab= true;
            for(let key in orderform){
                disab = orderform[key].validation && disab;
                
            }
            
            setdis(disab)
            console.log(dis)
           
        }
        const validateor=(value,rule)=>{
            let valid= true;
             if(!rule){
                 return true;
             }
             if(rule.require){
                 valid= value.trim() !== '' && valid;
             }
             if(rule.minlengt){
                 valid= value.length >= rule.minlengt && valid;
             }
             if(rule.maxlength){
                 valid= value.length <= rule.maxlength && valid;
             }
            
             return valid;
        }
        
        const input=dataonj.map(mp=>(
            <Input key={mp.id} proptype={mp.config.proptype}
             value={mp.config.value}
             tag={mp.config.tag}
            optiontxt={mp.config.optiontxt}
            validation={mp.config.validation}
            touch={mp.config.touch}
             change={(event)=>changehandller(event, mp.id)}></Input>
        ))
        const senddatahandller=()=>{
            const data={
                adress:orderform.adress.value,
                phoneNumber:orderform.phoneNumber.value,
                eshtrak: orderform.eshtrak.value
            }
            console.log(data)
        }
    return(
        <div style={{height:window.innerHeight,width:window.innerWidth,background:'#fff'}}>
            <div className={classes.head}></div>
             <div className={classes.section}>
                 <div className={classes.box_input}>{input}
               <Button disabl={!dis} click={senddatahandller} > ثبت سفارش</Button>
               </div> 
               </div>
        </div>
    );
}
const mapstatetoprops=state=>{
    return{

    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        
    }
}
export default connect(mapstatetoprops,mapdispatchtoprops)(Ordersumury);