import * as action from './actiontype';

export const rate=(rte,id)=>{
    return{
        type:action.Rating,
        rte:rte,
        id:id
    }
}
 export const filter=(txt)=>{
     return{
        type:action.FILTER,
        txt:txt
     }
 }
 export const addorder=(id)=>{
     console.log(id)
     return{
        type: action.ADDORDER,
        idadd:id
     }
        
 }

export const lessorder=(id)=>{
    return{
        type:action.LESSORDER,
        idless:id
    }
}
export const brenge=(id,tr)=>{
    return{
        type:action.BABRENG,
        tr:tr,
        idrice: id
    }
}
export const specially=(idsp,tr)=>{
    return{
        type:action.SPECIALLY,
        tr:tr,
        idsp:idsp
    }
}
export const setorder=()=>{
    return{
        type:action.STARTORDER
    }
}

export const failorder=()=>{
    return{
        type:action.FAILORDER
    }
}
export const initorder=()=>{
    return dispatch=>{

    }
}