import React from 'react';
import classes from './Input.css';

const Input=(props)=>{

    let inp= null;
    const classcss=[classes.input]
    if(!props.validation && props.touch){
            classcss.push(classes.notvalid)
    }
    switch(props.tag){
        case 'input':
            inp=<input className={classcss.join(' ')} {...props.proptype} {...props.value} onChange={props.change} />
            break;
        case 'input':
            inp=<input className={classcss.join(' ')}  {...props.proptype} {...props.value} onChange={props.change}/>
             break;
        case 'texterea':
            inp=<textarea className={classcss.join(' ')}  {...props.proptype} {...props.value} onChange={props.change} />
            break;
        case 'select':
            inp=<select className={classes.select_inp} value={props.value} onChange={props.change}>
                {props.optiontxt.opt.map((mp,id)=>(
                    <option key={id}>{mp.val}</option>
                ))}
            </select>
            break;
        default: <input className={classcss.join(' ')}  {...props.proptype} {...props.value} onChange={props.change} />
        break;
    }
    return(
        <div>
            {inp}
        </div>
    )
}
export default Input;