import React from 'react';
import Home from './continar/Home/Home';
import Ordersumury from './continar/ordersumury/ordersumury';
import {Switch, Route} from 'react-router-dom';
import AUTH from './continar/Auth/Auth';
import lastOrder from './components/lastOrder/lastOrder';


 function App(){


    let auth= <AUTH></AUTH>;
    if(true){
      auth=<Switch
      > <Route path="/" exact component={Home}/>
      <Route path="/order" component={Ordersumury}/>
      <Route path="/lastorder"  component={lastOrder}/>
      <Route path="/auth" component={AUTH}/>
      </Switch>
    }
  return(

      <div>
          {auth}
      </div>
  );
 }
