  import React,{useRef,useState, useEffect} from 'react';
  import classes from './cardfood.css';
  import {connect} from 'react-redux';
  import StarRating from 'react-star-ratings';
  
  import * as action from '../../../store/action/index';

  const Cardfood = (props)=>{
      const [rating,setrating]=useState(3)
        const [data, filterdata]=useState(props.card);
        const [selc, setselc]=useState([]);
        
        useEffect(()=>{
            const value= props.card.filter(id=>id.tipical === props.filt)
            filterdata(value)
            console.log(value)
        },[props.filt,props.card])


        const changespecial=(e,id)=>{
            //setselc(e.target.value)
            console.log(e.target.value)
            props.specialorder(id,e.target.value)
        }
        const changerice= (e,id)=>{
            props.riceorder( id,e.target.value)
        }
        const orderaddnum=(i)=>{
            props.addordered(i)
        }
        const orderlessnum=(id)=>{
            props.lessordered(id)
           
        }
        const ratinghandller=(e,id)=>{
        
              selc.push({id: id})
               const va= selc.filter(e=>(e.id === id))
                if(va.length === 1){
                    props.rate(e,id);
                    console.log(e,id)
                }
                console.log(selc,va)
        }
        
        let cards=null;
        if(props.card){
            cards= data.map(mp=>(
                <div className={classes.Cardfood} key={mp.id} >
                    
                    <div className={classes.im}><img src={mp.img} className={classes.food_img}/></div>
                   <div className={classes.ratings}>
                       {mp.ratings?<StarRating
                    rating={mp.ratings}
                    starRatedColor="#f53b3b"
                    changeRating={(e)=>ratinghandller(e,mp.id)}
                    numberOfStars={6}
                    name='rating'
                    starDimension="15px"
                     starSpacing="3px"
                    />:null}

                    <h3 className={classes.h3}>{mp.name}</h3>
                    
                    </div> 
                    <div className={classes.pragraf}><span className={classes.span}>قیمت</span>
                     <span className={classes.span}>{mp.price}</span> </div>
                     
                        {mp.tipical === 'kabab'? <div className={classes.card_select}><select  onChange={e=>changerice(e,mp.id)} className={classes.select}><option>بابرنج</option>
                         <option>بدون برنج</option></select></div>:null}
                         <div className={classes.card_select}><select className={classes.select}  onChange={e=>changespecial(e,mp.id)}><option>معمولی</option>
                         <option>ویژه</option></select></div>
                         <div className={classes.btn}>
                     <button className={classes.add} onClick={()=>orderaddnum(mp.id)} >شفارش <span className={classes.some_food} style={mp.some === 0?{display:'none'}:null}>{mp.some !== 0? mp.some:null}</span></button>
                        {mp.some > 0?<button className={classes.add} onClick={()=>orderlessnum(mp.id)}>حذف</button>: null}</div>
                </div>
            ))
        }
    return cards;
  } 
  const mapstatetoprops=state=>{
    return{
        card:state.card.foods,
        filt:state.card.filtertxt
    }
  }
  const mapdispatchtoprops=dispatch=>{
      return{
        addordered: (id)=>dispatch(action.addorder(id)) ,
        lessordered: (id)=>dispatch(action.lessorder(id)),
        specialorder:(id,tr)=>dispatch(action.specially(id,tr)),
        riceorder:(id,tr)=>dispatch(action.brenge(id,tr)),
        rate: (rte,id)=> dispatch(action.rate(rte,id))
      }
  }
  export default connect(mapstatetoprops,mapdispatchtoprops)(Cardfood);