import React,{useState} from 'react';
import classes from './navigation.css';
import {connect} from 'react-redux';
import * as action from '../../store/action/index';
const Navigation=(props)=>{
        
        const [prop, setprop]= useState('kabab')
        let classcss= classes.li;
        const setvalueclick=(event, prop)=>{
            console.log(prop)
            setprop(prop)
            props.onfilter(prop)
        }
        
    return(
        <ul className={classes.ul}>
            <li className={prop === 'kabab'? classes.active: classes.li} onClick={(e)=>setvalueclick(e, 'kabab')}  >کباب ها </li>
            <li className={prop === 'khoresh'? classes.active: classes.li} onClick={(e)=>setvalueclick(e, 'khoresh')}>خورش ها</li>
            <li className={prop === 'deser'? classes.active: classes.li} onClick={(e)=>setvalueclick(e, 'deser')}>دسرها</li>
            <li className={prop === 'drink'? classes.active: classes.li} onClick={(e)=>setvalueclick(e, 'drink')}>نوشیدنی ها</li>
            
        </ul>
    )
}
const mapstatetoprops=state=>{
    return{

    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        onfilter: (txt)=>dispatch(action.filter(txt))
    }
}
export default connect(mapstatetoprops,mapdispatchtoprops)(Navigation);